.App {
  text-align: center;
}

.priority {
  color: red;
  font-weight: bold;
  margin-top: 10px;
  padding: 2px;
  border: 2px dashed red;
}

.standard {
  color: white;
  font-family: "Kalam", cursive;
}

h4 {
  margin-top: 10px;
  padding: 4px;
  margin-bottom: 30px;
  overflow-wrap: break-word;
}

.doneTaskList {
  color: green;
  text-decoration-line: line-through;
  overflow-wrap: break-word;
  padding: 20px 30px 0px 30px;
}

.singleTask {
  position: relative;
  width: 270px;
  padding: 20px;
  margin: 10px;
  border-radius: 2vw;
  display: inline-block;
  border: 2px solid black;
  box-shadow: 0px 0px 10px 1px black;
}

.TasksToDoHeader {
  padding: 20px;
  padding-bottom: 50px;
}

.TasksToDoContainers {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
}

.control-buttons {
  text-align: end;
  margin-top: -10px;
  padding-bottom: 15px;
}

.singleTask p {
  position: absolute;
  top: 10px;
  left: 18px;
  font-size: 15px;
  font-family: "Audiowide", cursive;
  border-bottom: 2px solid rgb(0, 140, 255);
  border-block-end-style: groove;
}

.InputArea {
  padding-bottom: 100px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;

  // input task field
  .inputField {
    padding-top: 10px;
    padding-left: 80px;
    // task field
    .inp {
      position: relative;
      margin-top: 5px;
      width: 100%;
      max-width: 280px;
      .label {
        position: absolute;
        top: 16px;
        left: 0;
        font-size: 18px;
        color: #9098a9;
        font-weight: 500;
        transform-origin: 0 0;
        transition: all 0.2s ease;
      }
      .border {
        position: absolute;
        bottom: 0;
        left: 0;
        height: 2px;
        width: 100%;
        background: #0077ff;
        transform: scaleX(0);
        transform-origin: 0 0;
        transition: all 0.15s ease;
      }
      input {
        width: 100%;
        border: 0;
        font-family: inherit;
        padding: 20px 10px 10px;
        height: 48px;
        font-size: 18px;
        font-weight: 500;
        border-bottom: 2px solid #c8ccd4;
        background: none;
        border-radius: 0;
        color: #223254;
        transition: all 0.15s ease;
        &:hover {
          background: rgba(#223254, 0.03);
        }
        &:not(:placeholder-shown) {
          + span {
            color: #5a667f;
            transform: translateY(-20px) scale(0.75);
          }
        }
        &:focus {
          background: none;
          outline: none;
          border-bottom: 2px solid #0077ff;
          + span {
            color: #0077ff;
            transform: translateY(-20px) scale(0.75);
            .border {
              transform: scaleX(1);
            }
          }
        }
      }
    }

    // priority checked
    .squaredCheckbox {
      width: 20px;
      position: absolute;
      left: 230px;
    }
    .squaredCheckbox label {
      width: 17px;
      height: 17px;
      cursor: pointer;
      position: absolute;
      top: 0;
      left: 0;
      border-radius: 4px;
      box-shadow: inset 2px 2px 5px rgba(0, 0, 0, 0.5),
        0px 1px 0px rgba(255, 255, 255, 0.4);
    }
    .squaredCheckbox label:after {
      content: "";
      width: 13px;
      height: 7px;
      position: absolute;
      top: 5px;
      left: 4px;
      border: 3px solid #0c0c0c;
      border-top: none;
      border-right: none;
      background: transparent;
      opacity: 0;
      -webkit-transform: rotate(-45deg);
      transform: rotate(-45deg);
    }
    .squaredCheckbox input[type="checkbox"] {
      visibility: hidden;
    }

    .squaredCheckbox i {
      font-size: 14px;
      position: absolute;
      left: -10px;
      top: -1px;
      width: 150px;
    }

    .squaredCheckbox input[type="checkbox"]:checked + label:after {
      opacity: 1;
    }
  }

  // select type
  .selectBox {
    position: relative;
    padding: 20px;
    padding-left: 200px;

    b {
      font-weight: 400;
    }

    select {
      padding: 4px;
      font-size: 16px;
      border-radius: 10px;
      color: rgb(255, 255, 255);
      outline: none;
      cursor: pointer;
      width: 100px;

      &:hover {
        background: #444343d2;
      }

      option {
        background: white;
        color: black;
      }
    }
  }

  // calendar deadline
  .deadlineDate {
    padding-left: 20px;
    top: 10px;

    p {
      text-align: left;
      padding: 0;
      margin: 0;
    }
    input[type="date"] {
      border: 1px solid #c4c4c4;
      border-radius: 5px;
      background-color: #fff;
      padding: 3px 5px;
      box-shadow: inset 0 3px 6px rgba(0, 0, 0, 0.1);
      width: 160px;

      background: #fff
        url(https://cdn1.iconfinder.com/data/icons/cc_mono_icon_set/blacks/16x16/calendar_2.png)
        97% 50% no-repeat;
    }
    [type="date"]::-webkit-inner-spin-button {
      display: none;
    }
    [type="date"]::-webkit-calendar-picker-indicator {
      opacity: 0;
    }
  }

  // add task button
  .AddBtnContainer {
    #addBtn {
      margin-top: 50px;
      width: 100px;
      height: 40px;
      margin-left: -200px;
      transition: 0.5s;
      &:hover {
        transform: scale(1.1);
      }
    }

    p {
      padding-left: 60px;
    }
  }
}
